import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ConfigureComponent } from './configure.component';
import { ViewBillsComponent } from './view-bills/view-bills.component';
import { ModifyBillsComponent } from './modify-bills/modify-bills.component';


const routes: Routes = [
    
    {path:'',component: ConfigureComponent},
    {path:'view-bills',component: ViewBillsComponent},
    {path:'modify-bills',component: ModifyBillsComponent},
    
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ConfigRouting {}
